import React from "react";
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';

const SignIn = ({ onRouteChange }) => {
    return (
        <div style={{width : '20%', margin : 'auto', padding : '20px', border : '5px solid #1875b1', borderRadius : '8px'}}>
          <Form.Group className="mb-5" controlId="formBasicEmail">
            <Form.Label style={{display : 'flex', justifyContent : 'center',fontSize : '40px', fontWeight : '700'}}>Sign In</Form.Label><br />
            <Form.Label>Email address</Form.Label>
            <Form.Control type="email" placeholder="Enter email" />
          </Form.Group>
          <Form.Group className="mb-3" controlId="formBasicPassword">
            <Form.Label>Password</Form.Label>
            <Form.Control type="password" placeholder="Password" />
          </Form.Group>
          <Button variant="primary" type="submit" onClick={() => onRouteChange('homescreen')}>
            Submit
          </Button>
        </div>
    )
}
export default SignIn;