import React from 'react';
import Navigation from './components/Navigation/Navigation';
import Logo from './components/Logo/Logo';
import ImageFormLink from './components/ImageFormLink/ImageFormLink';
import Rank from './components/Rank/Rank';
import Particle from './components/Particles/Particle';
import FaceDetection from './components/FaceDetection/FaceDetection';
import SignIn from './components/SignIn/SignIn';
import Register from './components/Register/Register';

class App extends React.Component {
  constructor() {
    super()
    this.state = {
      input : '',
      imageURL : '',
      box : {},
      route : 'signin',
      isSignedIn : false
    }
  }

  onInputChange = (event) => {
    this.setState({input : event.target.value})
  }


  onSubmit = () => {
    this.setState({imageURL : this.state.input})

    const USER_ID = 'willianac';
    const PAT = '3d3dffec5b5c4a73a8968d5c5c2e2519';
    const APP_ID = 'my-first-application';
    const MODEL_ID = 'face-detection';
    const MODEL_VERSION_ID = '45fb9a671625463fa646c3523a3087d5';    
    const IMAGE_URL = this.state.input;

    const raw = JSON.stringify({
        "user_app_id": {
            "user_id": USER_ID,
            "app_id": APP_ID
        },
        "inputs": [
            {
                "data": {
                    "image": {
                        "url": IMAGE_URL
                    }
                }
            }
        ]
    });

    const requestOptions = {
        method: 'POST',
        headers: {
            'Accept': 'application/json',
            'Authorization': 'Key ' + PAT
        },
        body: raw
    };
    fetch("https://api.clarifai.com/v2/models/" + MODEL_ID + "/versions/" + MODEL_VERSION_ID + "/outputs", requestOptions)
        .then(response => response.json())
        .then(result => this.displayFaceBox(this.calculateBoundingBox(result)))
        .catch(error => console.log('error', error));
  }

  calculateBoundingBox = (data) => {
    const faceCoordinates = data.outputs[0].data.regions[0].region_info.bounding_box
    const image = document.getElementById('image')
    const largura = Number(image.width)
    const altura = Number(image.height)
    
    return {
      leftCol : faceCoordinates.left_col * largura,
      topRow : faceCoordinates.top_row * altura,
      rightCol : largura - (faceCoordinates.right_col * largura),
      bottomRow : altura - (faceCoordinates.bottom_row * altura)
    }
  }

  displayFaceBox = (box) => {
    console.log(box)
    this.setState({box : box})
  }

  onRouteChange = (route) => {
    if(route === 'signout') {
      this.setState({isSignedIn : false})
    } else if(route === 'homescreen') {
      this.setState({isSignedIn : true})
    }
    this.setState({route : route})
  }

  render () {
    return (
      <div>
        <Particle />
        <Navigation isSignedIn={this.state.isSignedIn} onRouteChange={this.onRouteChange}/>
        {this.state.route === 'homescreen'
        ? <div>
            <Logo />
            <Rank />
            <ImageFormLink onInputChange={this.onInputChange} onSubmit={this.onSubmit} />
            <FaceDetection imageURL={this.state.imageURL} box={this.state.box}/>
          </div>
        :
        (
          this.state.route === 'signin'
          ? <SignIn onRouteChange ={this.onRouteChange}/>
          : <Register onRouteChange ={this.onRouteChange}/>
        )
        }
      </div>
    );
  }
}

export default App;